<template>
  <div class="input">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-tabs class="mb10" v-model="tabPick" @tab-click="handleClick">
        <el-tab-pane label="基本情况" name="basic"></el-tab-pane>
        <el-tab-pane v-if="isReading" label="在校情况" name="school"></el-tab-pane>
        <el-tab-pane label="获奖情况" name="award"></el-tab-pane>
      </el-tabs>
      <transition-group mode="out-in" name="el-fade-in-linear">
        <add-form key="basic" v-show="tabPick === 'basic'"></add-form>
        <div key="resume" v-show="tabPick === 'school'">
          <el-input class="search-input" @keyup.enter.native="$search(schoolInfo)" v-model="schoolInfo.query.keywords"
                    placeholder="考试批次">
            <el-button @click="$search(schoolInfo)" slot="append" icon="el-icon-search"></el-button>
          </el-input>
          <table v-if="schoolInfo.list.length" class="school-table">
            <tr>
              <td>考试批次</td>
              <td v-for="item in list.thData">{{item}}</td>
              <td>总分</td>
              <td>年级排名</td>
              <td>年级进退步</td>
              <td>班级排名</td>
              <td>班级进退步</td>
            </tr>
            <tr v-for="item in list.tdData">
              <td>{{item.examBatchName}}</td>
              <td v-for="i in item.item">{{i}}</td>
              <td>{{item.totalScore}}</td>
              <td>{{item.totalSchoolRank}}</td>
              <td>{{item.totalSchoolRankDiff}}</td>
              <td>{{item.totalClassRank}}</td>
              <td>{{item.totalClassRankDiff}}</td>
            </tr>
          </table>
          <div class="no-data" v-show="!schoolInfo.noData">暂无数据</div>
          <paging :data.sync="schoolInfo"></paging>
        </div>
        <div key="award" v-show="tabPick === 'award'">

          <resize-table>
          <table class="table award-table">
            <thead>
            <tr>
              <th width="30%">奖项名称(须详细说明荣誉称号或获奖等级)</th>
              <th width="15%">获奖级别</th>
              <th width="25%">举办单位</th>
              <th width="15%">获奖时间</th>
              <th width="15%">操作</th>
            </tr></thead>
            <tbody>
            <tr v-for="item in award.list">
              <td>{{item.prizeName}}</td>
              <td>{{$enums.PrizeLevel.getName(item.prizeLevel)}}</td>
              <td>{{item.sponsor}}</td>
              <td>{{item.prizeDate | substrTime}}</td>
              <td>
                <span class="oper">查看</span>
                <span class="oper">下载</span>
              </td>
            </tr></tbody>
          </table>
          </resize-table>
          <no-data v-show="!award.noData"></no-data>
          <paging :data.sync="award"></paging>
        </div>
      </transition-group>

    </main-part>
  </div>

</template>
<script>
  import addForm from './Form.vue'

  export default {
    components: {
      addForm
    },
    data() {
      let isReading = this.$route.path === '/edu-affair/reading/detail'
      return {
        // 是否为在读生
        isReading,
        nav: [{name: isReading ? "在读生信息" : "学生信息", path: "go(-1)"}, {name: "学生详情", path: ""}],
        tabPick: 'basic',
        // 在校情况
        schoolInfo: {
          url: '/student/queryStudentPerformancePage.do',
          el: '.school-table',
          total: 0,
          noData: true,
          list: [],
          query: {
            id: this.$route.query.id,
            keywords: null
          }
        },
        // 获奖情况
        award: {
          url: '/student/queryStudentPrizeList.do',
          el: '.award-table',
          total: 0,
          noData: true,
          list: [],
          query: {
            id: this.$route.query.id,
            keywords: null
          }
        }
      }
    },
    computed:{
      'list': function () {
        let arr = []
        this.schoolInfo.list.forEach((e,i)=>{
          arr.push({
            examBatchName: e.examBatchName,
            totalClassRank: e.totalClassRank,
            totalClassRankDiff: e.totalClassRankDiff,
            totalLevel: e.totalLevel,
            totalSchoolRank: e.totalSchoolRank,
            totalSchoolRankDiff: e.totalSchoolRankDiff,
            totalScore: e.totalScore,
            item: []
          })
          e.data.forEach(n=>{
            arr[i].item.push((n.score ? n.score : '') +' '+ (n.level ? n.level : ''))
            arr[i].item.push(n.rank || '')
          })
        })
        let thArr = []
        this.schoolInfo.list[0].data.forEach(e=>{
          thArr.push(e.firstColName)
          thArr.push('年级排名')
        })
        let data = {
          tdData: arr,
          thData: thArr
        }
        return data
      }
    },
    methods: {
      // tab 切换的回调
      handleClick() {
        if (this.tabPick === 'school') {
          // this.$router.push({
          //   path: this.$route.path,
          //   query:{
          //     id: this.$route.query.id,
          //     type: this.$route.query.type,
          //     tabPick: this.tabPick
          //   }
          // })
          this.$search(this.schoolInfo)
        } else if (this.tabPick === 'award') {
          this.$search(this.award)
        }
      }
    },
    mounted() {
      // console.log(this.isReading)
    }
  }
</script>
<style lang="sass" scoped>
  .input
    .no-data
      color: $gray
      text-align: center
      line-height: 100px
    .search-input
      width: 260px
      margin-bottom: 15px
    .school-table
      border-collapse: collapse
      border-spacing: 0
      width: 100%
      td
        border: 1px solid #000
        padding: 5px 2px
        text-align: center
</style>
